import React, { Component } from 'react';
import * as Utils from './../utils.js';

class TextInput extends Component {

	setSelection(start, len = 0) {
		var input = this.getInputDOMNode();
		if (!input) {
			return;
		}

		var end = start + len;
		if ('selectionStart' in input && 'selectionEnd' in input) {
			input.selectionStart = start;
			input.selectionEnd = end;
		} else {
			var range = input.createTextRange();
			range.collapse(true);
			range.moveStart('character', start);
			range.moveEnd('character', end - start);
			range.select();
		}
	}

	getSelection() {
		var input = this.getInputDOMNode();
		var start = 0;
		var end = 0;

		if ('selectionStart' in input && 'selectionEnd' in input) {
			start = input.selectionStart;
			end = input.selectionEnd;
		} else {
			var range = document.selection.createRange();
			if (range.parentElement() === input) {
				start = -range.moveStart('character', -input.value.length);
				end = -range.moveEnd('character', -input.value.length);
			}
		}
		return {
			start: start,
			end: end,
			length: end - start
		};
	}

	getCursorPos() {
		return this.getSelection().start;
	}

	setCursorPos(pos) {
		this.setSelection(pos, 0);
	}

	isDOMElement(element) {
		return typeof HTMLElement === 'object'
			? element instanceof HTMLElement // DOM2
			: element.nodeType === 1 && typeof element.nodeName === 'string';
	}

	getInputDOMNode() {
		var input = this.input;
		if (!input) {
			return null;
		}

		if (this.isDOMElement(input)) {
			return input;
		}

		// React 0.13
		return React.findDOMNode(input);
	}

	setInputValue(value) {
		var input = this.getInputDOMNode();
		if (!input) {
			return;
		}

		this.value = value;
		input.value = value;
	}

	relocateCursorPos(commaText, cursorPosInDigit) {
		let pos = 0;
		for (pos = 0; pos < commaText.length && cursorPosInDigit > 0; pos++) {
			if (commaText[pos] !== ',') {
				cursorPosInDigit --;
			}
		}
		return pos;
	}

	render() {
		const {id, type, label, value, placeholder, prefix, suffix, onChange} = this.props;
		const isInputGroup = prefix || suffix;
		
		let whenChange = (e) => {
			let text = e.target.value;

			const sel = this.getSelection();

			const textBefore = text.substr(0, sel.start);
			const cursorPosInDigit = Utils.trimNonDigitCharacters(textBefore).length;

			const number = Utils.trimNonDigitCharacters(text) - 0;
			let formatted = Utils.formatNumber(number);
			
			this.setInputValue(formatted);
			
			if (onChange) onChange(number);

			const cursorPos = this.relocateCursorPos(formatted, cursorPosInDigit);

			this.setCursorPos(cursorPos);
		};

		return (
			<div>
				{isInputGroup && label ? <label htmlFor={id}>{label}</label> : ""}
				<div className={isInputGroup ? "input-group" : "form-group"}>
					{!isInputGroup && label ? <label htmlFor={id}>{label}</label> : ""}
					{prefix ? <span className="input-group-addon">{prefix}</span> : ""}
						<input
						type={type}
						className="form-control input-sm"
						id={id}
						value={Utils.formatNumber(value)}
						placeholder={placeholder}
						onChange={whenChange}
						ref={el => this.input = el} />
					{suffix ? <span className="input-group-addon">{suffix}</span> : ""}
				</div>
			</div>
		);
	}
}

export default TextInput


